/* eslint-disable @typescript-eslint/no-explicit-any */
"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import { useRouter } from "next/navigation"; // Router for navigation
import Script from "next/script"; // To load Razorpay
import { CheckIcon, SparklesIcon, Loader2 } from "lucide-react";
import { sectionVariant } from "@/lib/animations";

// --- AUTH CHECK (REPLACE THIS WITH YOUR ACTUAL AUTH HOOK) ---
// Example: const { session } = useSession(); const isLoggedIn = !!session;
const useAuth = () => {
  // TODO: Ikkada nee real logic pettu. Ippatiki 'true' peduthunna testing kosam.
  const isLoggedIn = true;
  return { isLoggedIn, userEmail: "test@example.com" };
};

export default function PricingSection() {
  const [currency, setCurrency] = useState<"USD" | "INR">("INR");
  const [loadingTier, setLoadingTier] = useState<string | null>(null);
  const router = useRouter();
  const { isLoggedIn, userEmail } = useAuth();

  const pricingTiers = [
    {
      name: "Free",
      id: "free",
      prices: { USD: 0, INR: 0 },
      frequency: "/ forever",
      description: "Perfect for testing the waters.",
      features: ["Works Offline", "21 Tasks", "1 Device"],
      cta: "Get Started",
      popular: false,
    },
    {
      name: "Basic",
      id: "basic",
      prices: { USD: 5, INR: 199 },
      frequency: "/ month",
      description: "For personal organization.",
      features: ["Unlimited Tasks", "Personal Diary", "2 Devices"],
      cta: "Start Basic",
      popular: false,
    },
    {
      name: "Pro",
      id: "pro",
      prices: { USD: 12, INR: 499 },
      frequency: "/ month",
      description: "For power users & creators.",
      features: ["Everything in Basic", "AI Insights", "5 Devices"],
      cta: "Go Pro",
      popular: true,
    },
    {
      name: "Ultra",
      id: "ultra",
      prices: { USD: 25, INR: 999 },
      frequency: "/ month",
      description: "For teams & obsessives.",
      features: ["Everything in Pro", "Unlimited Everything", "Collaboration"],
      cta: "Get Ultra",
      popular: false,
    },
  ];

  // --- MAIN LOGIC HANDLER ---
  const handlePlanClick = async (tier: any) => {
    // 1. Login Check
    if (!isLoggedIn) {
      router.push("/auth/signup"); // Login lekapothe Signup page ki pampinchu
      return;
    }

    // 2. Free Plan Logic
    if (tier.name === "Free") {
      router.push("/app/dashboard"); // Direct Dashboard ki
      return;
    }

    // 3. Paid Plan Logic (Razorpay)
    setLoadingTier(tier.name);
    try {
      await startPayment(tier);
    } catch (error) {
      console.error(error);
      alert("Payment initiation failed.");
      setLoadingTier(null);
    }
  };

  const startPayment = async (tier: any) => {
    const amount = currency === "INR" ? tier.prices.INR : tier.prices.USD;

    // Step A: Create Order
    const res = await fetch("/api/payment/create-order", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ amount: amount }),
    });
    const order = await res.json();

    if (!order.id) throw new Error("Order creation failed");

    // Step B: Razorpay Options
    const options = {
      key: process.env.NEXT_PUBLIC_RAZORPAY_KEY_ID,
      amount: order.amount,
      currency: order.currency,
      name: "TaskGlyph",
      description: `${tier.name} Plan Subscription`,
      order_id: order.id,
      handler: async function (response: any) {
        // Step C: Verify & Update DB
        const verifyRes = await fetch("/api/payment/verify", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            razorpay_payment_id: response.razorpay_payment_id,
            razorpay_order_id: response.razorpay_order_id,
            razorpay_signature: response.razorpay_signature,
            plan_name: tier.name, // IMPORTANT: Sending plan name to backend
          }),
        });

        const verifyData = await verifyRes.json();

        if (verifyData.success) {
          // SUCCESS MESSAGES
          let msg = "Payment Successful!";
          if (tier.name === "Basic")
            msg = "Basic Plan Activated! Enjoy Unlimited Access.";
          if (tier.name === "Pro")
            msg = "Welcome to Pro! AI Features Unlocked. ðŸš€";
          if (tier.name === "Ultra")
            msg = "Ultra Tier Unlocked! You are now a power user. ðŸ”¥";

          alert(msg);

          // RELOAD TO UPDATE NAVBAR & DASHBOARD
          window.location.href = "/app/dashboard";
        } else {
          alert("Payment Verification Failed.");
          setLoadingTier(null);
        }
      },
      prefill: { email: userEmail },
      theme: { color: tier.popular ? "#0f172a" : "#4f46e5" },
    };

    const rzp = new (window as any).Razorpay(options);
    rzp.open();
    rzp.on("payment.failed", () => {
      alert("Payment Failed");
      setLoadingTier(null);
    });
  };

  return (
    <motion.section
      id="pricing"
      variants={sectionVariant}
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, amount: 0.1 }}
      className="relative py-24 overflow-hidden bg-slate-50"
    >
      {/* Load Razorpay Script */}
      <Script src="https://checkout.razorpay.com/v1/checkout.js" />

      {/* Background Decor */}
      <div className="absolute top-0 left-1/2 -translate-x-1/2 w-[1000px] h-[400px] bg-indigo-200/20 blur-[100px] rounded-full -z-10" />

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header & Toggle */}
        <div className="flex flex-col items-center text-center">
          <h2 className="text-4xl font-black text-slate-900 tracking-tight sm:text-5xl">
            Transparent Pricing
          </h2>
          <p className="mt-4 max-w-2xl text-lg text-slate-600">
            Choose the perfect plan for your workflow.
          </p>

          {/* Currency Toggle Switch */}
          <motion.div className="mt-8 bg-white p-1.5 rounded-full border border-slate-200 shadow-sm inline-flex relative">
            <motion.div
              layout
              className={`absolute top-1.5 bottom-1.5 w-[50%] rounded-full bg-slate-900 shadow-md z-0 transition-all duration-300 ${
                currency === "USD" ? "left-[50%]" : "left-1.5"
              }`}
            />
            <button
              onClick={() => setCurrency("INR")}
              className={`relative z-10 px-6 py-2 rounded-full text-sm font-bold transition-colors ${
                currency === "INR" ? "text-white" : "text-slate-600"
              }`}
            >
              ðŸ‡®ðŸ‡³ INR (â‚¹)
            </button>
            <button
              onClick={() => setCurrency("USD")}
              className={`relative z-10 px-6 py-2 rounded-full text-sm font-bold transition-colors ${
                currency === "USD" ? "text-white" : "text-slate-600"
              }`}
            >
              ðŸ‡ºðŸ‡¸ USD ($)
            </button>
          </motion.div>
        </div>

        {/* Pricing Grid */}
        <div className="mt-16 grid grid-cols-1 gap-8 md:grid-cols-2 lg:grid-cols-4 items-start">
          {pricingTiers.map((tier) => {
            const isPro = tier.popular;
            const isLoading = loadingTier === tier.name;

            return (
              <motion.div
                key={tier.name}
                className={`relative flex flex-col p-6 rounded-3xl transition-all duration-300 group
                  ${
                    isPro
                      ? "bg-slate-900 text-white shadow-2xl scale-105 z-10"
                      : "bg-white text-slate-900 border border-slate-200 shadow-xl hover:-translate-y-1"
                  }
                `}
              >
                {/* Popular Badge */}
                {isPro && (
                  <div className="absolute -top-4 left-1/2 -translate-x-1/2 bg-gradient-to-r from-indigo-500 to-purple-500 text-white px-4 py-1 rounded-full text-xs font-bold uppercase tracking-widest shadow-lg flex items-center gap-1">
                    <SparklesIcon className="w-3 h-3" /> Most Popular
                  </div>
                )}

                <div className="mb-5">
                  <h3
                    className={`text-lg font-bold ${
                      isPro ? "text-indigo-300" : "text-indigo-600"
                    }`}
                  >
                    {tier.name}
                  </h3>
                  <p
                    className={`mt-2 text-sm ${
                      isPro ? "text-slate-300" : "text-slate-500"
                    }`}
                  >
                    {tier.description}
                  </p>
                </div>

                <div className="flex items-baseline gap-1 mb-6">
                  <span className="text-4xl font-black tracking-tight">
                    {currency === "INR" ? "â‚¹" : "$"}
                    {currency === "INR" ? tier.prices.INR : tier.prices.USD}
                  </span>
                  <span className="text-sm font-medium text-slate-400">
                    {tier.frequency}
                  </span>
                </div>

                <ul className="flex-grow space-y-4 mb-8">
                  {tier.features.map((feature) => (
                    <li key={feature} className="flex items-start gap-3">
                      <CheckIcon
                        className={`w-4 h-4 ${
                          isPro ? "text-indigo-300" : "text-indigo-600"
                        }`}
                      />
                      <span
                        className={`text-sm ${
                          isPro ? "text-slate-300" : "text-slate-600"
                        }`}
                      >
                        {feature}
                      </span>
                    </li>
                  ))}
                </ul>

                <button
                  onClick={() => handlePlanClick(tier)}
                  disabled={!!loadingTier}
                  className={`w-full py-3.5 px-6 rounded-xl text-center text-sm font-bold transition-all duration-300 shadow-md flex items-center justify-center gap-2
                    ${
                      isPro
                        ? "bg-white text-slate-900 hover:bg-indigo-50"
                        : "bg-slate-900 text-white hover:bg-slate-800"
                    }
                    ${!!loadingTier && "opacity-50 cursor-not-allowed"}
                  `}
                >
                  {isLoading ? (
                    <Loader2 className="animate-spin w-4 h-4" />
                  ) : (
                    tier.cta
                  )}
                </button>
              </motion.div>
            );
          })}
        </div>
      </div>
    </motion.section>
  );
}
